import React, { useState, useEffect } from "react";
import "./style.css";
import { useParams } from "react-router-dom";
import ReactMarkdown from "react-markdown";
import GoogleAdvertise from "react-adsense-google";

const Post: React.FC = () => {
  const { "*": filePath } = useParams<{ "*": string }>();
  const [content, setContent] = useState<string | null>(null);
  const [title, setTitle] = useState<string | null>(null);

  useEffect(() => {
    if (filePath) {
        const encodedPath = filePath.split('/').map(encodeURIComponent).join('/');
      fetch(`/pages/${encodedPath}`)
        .then((res) => res.text())
        .then(setContent)
        .catch(console.error);
    }
  }, [filePath]);

  if (!filePath) {
    return <h1>File not found</h1>;
  }

  const components = {
    img: ({ src, alt }: { src?: string; alt?: string }) => {
      if (!src) return <img alt={alt} />;
      const basePath = filePath.substring(0, filePath.lastIndexOf('/'));
      const resolvedSrc = `/pages/${basePath}/${src}`.replace(/\/+/g, '/').replace(/\/\.\//g, '/');
      return <img src={resolvedSrc} alt={alt} />;
    },
  };

  return (
    <div className="post-content" >
        <div className="post-title"></div>

        <GoogleAdvertise
        adClient="ca-pub-9500744719024251"
        adSlot="6355071382"
        style={{'display': 'block'}}
        adLayoutKey="-6t+ed+2i-1n-4w"
        adFormat='fluid'
        fullWidthResponsive='true'
      />


      <ReactMarkdown components={components}>
        {content || "Loading..."}
      </ReactMarkdown>
    </div>
  );
};

export default Post;
